.happy-loader-overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;    
    z-index: 100500;
    overflow: hidden;    
}

.happy-loader-bar{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    width: 1%;
    height: 2%;
    background: #fff; 
    margin: 0 auto;
}

.happy-loader-bar-center{
    position: fixed;
    left: 0;
    right: 0;
    top: 55%;
    width: 1%;
    height: 1%;
    background: #fff; 
    margin: 0 auto;
}

.happy-loader-bar-screen{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 1%;
    height: 100%;
    background: #fff; 
    margin: 0 auto;
}

.happy-loader-informer{
    position: fixed;
    left: 50%;
    top: 50%;
    color: #fff;
    font-family: sans-serif;
    font-size: 1em;
    line-height: 1em;
    margin: -1em 0 0 -1em; 
}